import { Vazirmatn } from "next/font/google"
const vazir = Vazirmatn ()
export default function layout({ children }: { children }) {
    return (
        <html lang="en">
            <body>
                <nav style={{
                    backgroundColor: "blue",
                    padding: "30px",
                    textAlign: "center",
                    color: "white"
                }}>
                    This is the nav
                </nav>
                {children}
                <footer style={{
                    backgroundColor: "gray",
                    padding: "12px",
                    textAlign: "center",
                    color: "white"
                }}>
                    <h1>footer</h1>
                </footer>
            </body>
        </html>
    )
}